export default 
{
	data() 
	{
		return 
		{
			gamelist3:[]
		}
	},
	onLoad:function()
	{
		uni.request({
			url: 'http://47.103.206.26:8080/miniprogram/list/release/games',
			method: 'GET',
			data: {},
			success: res => {
				this.gamelist3=res.data.gameList;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: 
	{
		opengameinfo(e){
			var gameid=e.currentTarget.dataset.gameid;
			uni.navigateTo({
				url: '../gameinfo/gameinfo?gameid='+gameid
			}
			
			);
		},
	}
}
